<template>
  <div>
    <div class="alert-container" v-if="alertActive">
      <vs-alert :active.sync="alertActive" :color="alertVariant" closable icon-pack="feather" close-icon="icon-x" class="mb-5">
        <p>{{ alertMessage }}</p>
      </vs-alert>
    </div>

    <nv-card class="mb-base">
      <div class="flex flex-wrap-reverse items-center mb-base">
        <vs-button class="p-3 mr-4" @click="cancel" color="primary" type="filled" icon="fa-chevron-left" icon-pack="far">{{ $t('BACK') }}</vs-button>
      </div>
      <div id="loading-container" class="vs-con-loading__container">
        <div class="nv-row mb-4">
          <div class="nv-col w-full">
            <label class="vs-input--label">{{ $t('OLD_PASSWORD') }}</label>
            <password class="w-full" v-model="oldPassword" placeholder="" :toggle="true" :badge="false" :showStrengthMeter="false" autocomplete="off"/>
          </div>
        </div>
        <div class="nv-row mb-5">
          <div class="nv-col w-full">
            <label class="vs-input--label">{{ $t('NEW_PASSWORD') }}</label>
            <password class="w-full" v-model="newPassword" placeholder="" :toggle="true" autocomplete="off"/>
          </div>
        </div>
        <div class="nv-row mb-4">
          <div class="nv-col w-full">
            <label class="vs-input--label">{{ $t('NEW_PASSWORD_VERIFY') }}</label>
            <password class="w-full" v-model="newPasswordVerify" placeholder="" :toggle="true" :badge="false" :showStrengthMeter="false" autocomplete="off"/>
          </div>
        </div>

        <div class="nv-row">
          <div class="nv-col w-full">
            <div class="flex justify-end">
              <vs-button class="px-5 py-3" @click="changeUserPassword" color="primary" type="filled">{{ $t('SUBMIT') }}</vs-button>
            </div>
          </div>
        </div>
      </div>
    </nv-card>
  </div>
</template>

<script>
import AlertMixin from '@/mixins/commons/AlertMixin'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import SecurityService from '@/services/nucleus/SecurityService'
import Password from 'vue-password-strength-meter'

export default {
  name: 'change-password',
  mixins: [AlertMixin, LoadingMixin],
  components: {
    Password
  },
  data: function () {
    return {
      oldPassword: '',
      newPassword: '',
      newPasswordVerify: ''
    }
  },
  methods: {
    changeUserPassword () {
      let self = this
      this.startLoading('loading-container')
      SecurityService.changeUserPassword({
        oldPassword: this.oldPassword,
        newPassword: this.newPassword,
        newPasswordVerify: this.newPasswordVerify
      }).then(function (response) {
        self.stopLoading('loading-container')
        self.showSuccessAlert(response.data.messages)
      }).catch(function (error) {
        self.stopLoading('loading-container')
        self.showDangerAlert(error.response.data.errors)
      })
    },
    cancel () {
      this.$router.back()
    }
  }
}
</script>
